<template>
<div>
<div id='banner'>
<div id="Img">
    <img src="https://www.givenchy.com/dw/image/v2/BBRT_PRD/on/demandware.static/-/Library-Sites-shared_GIV_Library/default/dwc46b4ac8/Home/FW19/MEN/MenFW19_HP_mobile.jpg?sfrm=jpg" alt="" class='on'>
    <img src="https://www.givenchy.com/dw/image/v2/BBRT_PRD/on/demandware.static/-/Library-Sites-shared_GIV_Library/default/dw8ed4910a/Home/SS19/SS19Campaign_HP_mobile.jpg?sfrm=jpg" alt="" >
    <img src="https://www.givenchy.com/dw/image/v2/BBRT_PRD/on/demandware.static/-/Library-Sites-shared_GIV_Library/default/dw61c17f37/Home/SPRING%2019/Shop%20the%20look/Shopthelook_W_Spring19_HP_mobile.jpg?sfrm=jpg" alt="">
    <img src="https://www.givenchy.com/dw/image/v2/BBRT_PRD/on/demandware.static/-/Library-Sites-shared_GIV_Library/default/dw730b50e8/Home/SPRING%2019/Shop%20the%20look/Shopthelook_M_Spring19_HP_mobile2.jpg?sfrm=jpg" alt="">
</div>
<ul id='banner-title'>
    <li class='on'>MEN WINTER 2019 COLLECTION</li>
    <li>2019春夏系列广告</li>
    <li>选购女士造型</li>
    <li>选购男士造型</li>
</ul>
<div id="Button">
    <div id='button'>
    <div class='on'></div>
    <div></div>
    <div></div>
    <div></div>
   </div>
</div>
<ul id='banner-title-small'>
    <li class='on'>发现</li>
    <li>发现</li>
    <li>SHOP THE LOOKS FROM THE SPRING 2019 COLLECTION</li>
    <li>SHOP THE LOOKS FROM THE SPRING 2019 COLLECTION</li>
</ul>

</div>

<div class='indexShop'>
    <p>在线购买</p>
    <div v-for='shop in shopup' :key='shop.index' class='indexShopdiv' @click='List(shop.msg)'>
  <router-link to="/index/List">  <img :src="shop.img" alt=""></router-link>
    <p>{{shop.msg}}</p>
    </div>
    <div class="indexNav">
        <p>女士</p>
        <ul>
            <li v-for='nav in nav' :key='nav.index'>{{nav}}</li>
        </ul>
       
        <p>男士</p>
        <ul>
            <li v-for='nav in nav' :key='nav.index'>{{nav}}</li>
        </ul>
    </div>
     <div v-for='shop in shopdown' :key='shop.index' class='indexShopdiv' @click='List2(shop.msg)'>
    <img :src="shop.img" alt="">
    <p>{{shop.msg}}</p>
    </div>
    <div class='modelling'>查看造型</div>
  
</div>

</div>
</template> 
<script>
import Axios from 'axios'
export default {
    data(){
        return{
          shopup:[],
          shopdown:[],
          nav:[]
        }
    },methods:{
        List(title){
            this.$router.push('/index/list'+title);
        },
        List2(title){
            this.$router.push('/index/list'+title);
        }
   
    },
    mounted(){
        let url='./static/data/index.json';
         let oBannerimg=document.getElementById('Img').getElementsByTagName('img');
         let oButton=document.getElementById('button').getElementsByTagName('div');
         let oBannerTitle=document.getElementById('banner-title').getElementsByTagName('li');
         let oBannerTitleSmall=document.getElementById('banner-title-small').getElementsByTagName('li');
        let sex=0;
        let timer=null;
         for(let i=0;i<oButton.length;i++){
             oButton[i].onmouseover=()=>{
                 for(let a=0;a<oButton.length;a++){
                     oBannerimg[a].className='';
                     oButton[a].className='';
                     oBannerTitle[a].className='';
                     oBannerTitleSmall[a].className='';
                 }
                 oBannerimg[i].className='on';
                 oButton[i].className='on';
                 oBannerTitle[i].className='on';
                 oBannerTitleSmall[i].className='on';
                 sex=i;             
             }
            
         }
  timer=setInterval(()=>{
             if(sex>3){
                 sex=0;
             }
             for(let i=0;i<oBannerimg.length;i++){
                 oBannerimg[i].className='';
                 oBannerimg[i].style.animation='';
                 oBannerTitle[i].className='';
                 oBannerTitleSmall[i].className='';
                 oButton[i].className='';
             }
            oBannerimg[sex].className='on';
            oBannerimg[sex].style.animation='Gradual 3s 1';
            oBannerTitle[sex].className='on';
            oBannerTitleSmall[sex].className='on';
             oButton[sex].className='on';
              
            sex++;
          
         },3000)
     
     Axios.get(url).then(res=>{
        this.shopup=res.data.shop.slice(0,2);
        this.shopdown=res.data.shop.slice(2,6);
        this.nav=res.data.nav;
  

     }).catch(
         ()=>{
             alert('数据错误');
         }
     )
   
    }
}
</script>  
<style>
#banner{
    position: relative;
     height:8.6rem;
    
}
#banner-title{
   width:100%;
    position:absolute;
    top:3.7rem;
      z-index:8888;
}
#banner-title>li{
    color:#fff;
    font-size:0.5rem;
    font-weight:bold;
    text-align: center;
    line-height: 0.6rem;
    display:none;
}
#banner-title>li.on{
    display:block;
}
 #Img{
    
     position:relative;
     height:8.6rem;
     overflow: hidden;
 } 
 #Img img{
     position:absolute;
     top:0;
     left:0;
     z-index:1;
     display: none;
 }
  #Img img.on{
      display: block;
      z-index:8888;
  }
 @keyframes Gradual{
    0%{
        opacity:0.1;
    }
    20%{
        opacity: 1;
    }
    100%{
        opacity: 1;
    }
}
 #Button{
     width:100%;
    height:1.2rem;
       position:absolute;
    bottom:1rem;
 display:flex;
 justify-content: center;
   z-index:8888;
 }
#button{
    width:1.05rem;
    height:1.05rem;
    position:relative;
  
}
#button div{
    height:0.45rem;
    width:0.45rem;
    position:absolute;
    background: rgba(0,0,0,0.6);
}
#button div.on{
    background: #fff;
}
#button div:nth-of-type(1){
    top:0;
    left:0;
}
#button div:nth-of-type(2){
    top:0;
    right:0;
}
#button div:nth-of-type(3){
    bottom:0;
    right:0;
}
#button div:nth-of-type(4){
    left:0;
    bottom:0;
}
#banner-title-small{
    width:100%;
    position:absolute;
    bottom:0.2rem;
    z-index:8888;
  
}
#banner-title-small li{
    width:100%;
    text-align:center;
    color:#fff;
    font-size:0.2rem;
    font-weight:bold; 
     display:none;
     line-height:0.3rem;
}
#banner-title-small li.on{
    display:block;
}
.indexShop>p{
    text-align: center;
    margin:0.6rem 0;
    font-size:0.32rem;
}
.indexShopdiv{
position:relative;
margin-bottom:0.2rem;
}
.indexShopdiv p{
    width:100%;
    position:absolute;
    top:2.7rem;
    left:0;
    text-align: center;
    color:#fff;
    font-size:0.5rem;
    font-weight:bold;
}
.indexNav{
    margin:0.5rem 0;
    padding:0 3%;
}
.indexNav>p{
    text-align: center;
    font-size:0.32rem;

}
.indexNav ul{
    margin:0.35rem 0;
    overflow: hidden;
}
.indexNav ul:nth-of-type(1){
    border-bottom:1px solid #999;
    padding-bottom:0.32rem;
}
.indexNav ul li{
    border:1px solid;
    font-weight:bold;
    width:34%;
    float:left;
    margin-left:10.2%;
    margin-bottom:0.3rem;
    text-align: center;
}
.modelling{
    height:1.2rem;
    width:92%;
    background: #000;
    color:#fff;
    font-weight: bold;
    text-align: center;
    line-height: 1.2rem;
    margin:0.6rem auto 1rem auto;
}
</style>


