<template>
<div>
    <div class='biaoti'><p>我的收藏</p></div>
 <div class='Collection'>
     
   <dl v-for='list in collectio' class='width47' :key='list.index'>
    <dt><img :src="list.img[0]" alt=""> </dt>
    <dd class='List-dd1'><strong>{{list.title}}</strong></dd>
    <dd class='List-dd2'>€{{list.money}}</dd>
    <dd class='XIN'><span>{{list.pz}}</span><span><img src="../../assets/img/xin2.png" alt=""></span></dd>
    <dd @click='go(list.id)'><router-link to="/index/Shop">去购买</router-link></dd>
</dl>
 </div>
<div class='More'>
     <p>显示{{collectio.length}}条，共{{collection.length}}条</p>
 <p @click='more()'>查看更多收藏</p>
</div>

</div>
</template> 
<script>
import Axios from 'axios'
export default {
    data(){
        return{
           collection:[],
           collectio:[],
        }
    },
    methods:{
       go(id){
      this.GLOBAL.shop(id);
       },
       more(){
       this.collectio=this.collection;
       },
    },
    mounted(){
        let url='./static/data/shop.json';
        Axios.get(url).then(res=>{
          for(let i=0;i<res.data.shop.length;i++){
              for(let j=0;j<res.data.shop[i].components.length;j++){
                  for(let n=0;n<this.GLOBAL.arr.length;n++){
                      if(res.data.shop[i].components[j].id==this.GLOBAL.arr[n]){
                          this.collection.push(res.data.shop[i].components[j]);
                      }
                  }
              }
          }
         this.collection=this.collection.reverse();
         this.collectio=this.collection.slice(0,10);
        }).catch(
            ()=>{
                alert('数据错误');
            }
        )
  
     
    }
}
</script>  

<style>
.biaoti{
    height:1.2rem;
    margin-bottom:1rem;
}
 .biaoti>p{
width:100%;
text-align: center;
font-size:0.3rem;
font-weight: bold;
line-height:1.2rem;
height:1.2rem;
border-bottom:1px solid #999;
position:fixed;
z-index:999;
background: #fff;
 }
 .Collection{
     overflow: hidden;
     width:97%;
       padding:0 3% 0 0;
 }
  .Collection>dl{
    margin-left:3.5%;
     float:left;
     width:46.5%;
     margin-bottom:0.3rem;
     position:relative;
  }   
  .Collection>dl dd:nth-of-type(3){
      position:absolute;
      top:0.1rem;
  }
  .Collection>dl dd:nth-of-type(4){
      position:absolute;
      bottom:0.9rem;
      background: #000;
      
      font-size:0.2rem;
      width:100%;
      text-align: center;
      padding:0.05rem 0;
  }
   .Collection>dl dd:nth-of-type(4) a{
color:#fff;
   }
 .More>p:nth-of-type(1){
      width:92%;
      height:1rem;
      line-height:1rem;
      margin:0 auto;
      border-bottom:1px solid;
      text-align: center;
      font-size:0.2rem;
  }
  .More>p:nth-of-type(2){
      text-align: center;
      line-height: 1rem;
      height:1rem;
      font-weight: bold;
      font-size:0.2rem;
      margin-bottom:0.8rem;
  }
</style>


