<template>
<div>
     
    <div class="Msg-img">
        <span class='lt' @click='spanleft()'>&lt;</span><span class='gt' @click='spanright()'>&gt;</span>
       <v-touch @swipeleft='swipeleft()' @swiperight='swiperight()'>
        <ul>
            <li v-for='img in msg.img' :key='img.id'><img :src="img" alt=""></li>
        </ul>
     </v-touch>
     <p @click='tianjia()'>添加至心愿清单<span class='tianjia'>添加成功</span></p>
    </div>
    <div class="Msg-msg">
        <p><strong>{{msg.title}}</strong></p>
        <p>€{{msg.money}}</p>
        <div class='msg-msg'>
          <p @click='display()'>{{more}}</p>
            <div class='msg-div' v-show='show' >
               <p>{{msg.msg}}</p>
               <p>{{msg.number}}</p>
               <dl>
                   <dt>版型</dt>
                   <dd>{{msg.style}}</dd>
               </dl>
               <dl>
                   <dt>其他</dt>
                   <dd>{{msg.other}}</dd>
               </dl>
            </div>
            </div>
    </div>
    <div class="Size" v-show='size'>
        <p>{{numb}}</p>
        <div>
            <ul>
                <li @click='chima(38)'>38</li>
                <li @click='chima(39)'>39</li>
                <li @click='chima(40)'>40</li>
                <li @click='chima(41)'>41</li>
                <li @click='chima(42)'>42</li>
                <li @click='chima(43)'>43</li>
                <li @click='chima(44)'>44</li>
            </ul>
        </div>
    </div>
    <div class="Msg-add">
        <p @click='shop(msg.id)'>添加到购物车</p>
        <p>到店寻找</p>
    </div>
    <div class="Msg-help">
     <ul>
         <li class='li1' @click='li1()'>需要我们帮助？</li>
         <li class='li2' @click='li2()'>免费送货和退货</li>
     </ul>
     <div>
         <div class='Msg-help-div1'>
我们的顾问团队可于每周周一至周六，巴黎时间早上9点到晚上8点(北京时间下午5点到次日凌晨2点)以英语或法语为您提供协助。（通话价格依据呼叫该地区座机号码的费用）</div>
         <div class='Msg-help-div2'>
             −	快递两个工作日送达<br>
             −	两天内在门店取货<br>
             −	网上退货或门店退货<br>
             根据您购买的商品，您还可以免费享受以下服务：<br>
             −	巴黎当天优先发货<br>
             −	当日到店取货（巴黎门店）
         </div>
     </div>
    
    </div>
</div>
</template>

<script>
import Axios from 'axios'
export default {
 data(){
     return{
        msg:[],
        scroll:0,
        more:'更多细节+',
        show:false,
        numb:'尺码',
        size:false,
     }
 },methods:{
     tianjia(){
         let oTianjia=document.getElementsByClassName('tianjia')[0];
           this.GLOBAL.setId(this.$route.params.Id);
           oTianjia.style.animation='dh 1s 1';
         
     },
 swipeleft(){
     let oMsgImg=document.getElementsByClassName('Msg-img')[0].getElementsByTagName('ul')[0];
     let oLt=document.getElementsByClassName('lt')[0];
     let oGt=document.getElementsByClassName('gt')[0];
   if(this.scroll>=5){
     this.scroll=5;
      oGt.style.opacity='0';
      oGt.style.animation='ltgt2 0.4s 1';
     }  
     else{
         this.scroll++;
         oGt.style.opacity='1'; 
         oGt.style.animation='ltgt 0.4s 1';
         }
 oMsgImg.style.marginLeft=-this.scroll*parseInt(100)+'%';
 oLt.style.opacity='1';
   oLt.style.animation='ltgt 0.4s 1';


 },
 swiperight(){
       let oLt=document.getElementsByClassName('lt')[0];
     let oGt=document.getElementsByClassName('gt')[0];
  if(this.scroll<=0){
      this.scroll=0;
      oLt.style.opacity='0';
      oLt.style.animation='ltgt2 0.4s 1';
  }else{
      this.scroll--;
      oLt.style.opacity='1'; 
      oLt.style.animation='ltgt 0.4s 1';
  }
   let oMsgImg=document.getElementsByClassName('Msg-img')[0].getElementsByTagName('ul')[0];
  oMsgImg.style.marginLeft=-this.scroll*parseInt(100)+'%';
 console.log('右',this.scroll);
   oGt.style.opacity='1';
   oGt.style.animation='ltgt 0.4s 1';
 },
 spanleft(){
     this.swiperight();
 },
 spanright(){
     this.swipeleft();
 },
 display(){
    this.show=!this.show;
  if(this.show){
      this.more='更少细节-'
  }else{
      this.more='更多细节+'
  }
 },
 chima(num){
     this.numb=num;
 
 },li1(){
 let oLi1=document.getElementsByClassName('li1')[0];
 let oLi2=document.getElementsByClassName('li2')[0];
 let odiv2=document.getElementsByClassName('Msg-help-div2')[0];
 let odiv1=document.getElementsByClassName('Msg-help-div1')[0];
  oLi1.style.borderBottom='2px solid';
  oLi2.style.borderBottom='none';  
  odiv1.style.zIndex='888';
    odiv2.style.zIndex='2';
 },
 li2(){
 let oLi1=document.getElementsByClassName('li1')[0];
 let oLi2=document.getElementsByClassName('li2')[0];
 let odiv2=document.getElementsByClassName('Msg-help-div2')[0];
 let odiv1=document.getElementsByClassName('Msg-help-div1')[0];
  oLi2.style.borderBottom='2px solid';
  oLi1.style.borderBottom='none';
    odiv1.style.zIndex='2';
    odiv2.style.zIndex='888';
 },
 shop(ID){
     this.GLOBAL.shop(ID);
 }
 },
 mounted(){
      let url='./static/data/shop.json';
      let patt=new RegExp("鞋","g");
      Axios.get(url).then(res=>{ 
          for(let i=0;i<res.data.shop.length;i++){
              for(let j=0;j<res.data.shop[i].components.length;j++){
                 if(res.data.shop[i].components[j].id==this.$route.params.Id){
                     this.msg=res.data.shop[i].components[j];
                 }
              }
          }
        //   for(let i=0;i<res.data.shop.length;i++){
        //      for(let j=0;j<res.data.shop[i].components.length;j++){
        //          for(let n=0;n<this.GLOBAL.zuijin.length;n++){
        //              if(res.data.shop[i].components[j].id==this.GLOBAL.zuijin[n]){
        //                  this.zuijin.push(res.data.shop[i].components[j])
        //              }
        //          }
        //      }
        //   }
          this.size=patt.test(this.msg.title)
       
       
      }).catch(()=>{
          alert('数据错误');
      })
   

 }

}
</script>
<style>
 .Msg-img{
     overflow:auto;
    position:relative;
  
 }  
 .Msg-img::-webkit-scrollbar{
     display:none;
 }
 .Msg-img ul{
     overflow:hidden;
     width:600%;
    
     display:flex;
     justify-content: flex-start;
     margin-left:0;
    transition:margin-left 0.3s;
    
 }  
  .Msg-img ul li{
      width:100%;
    
  }
  .Msg-img ul li img{
      width:100%;
  }
  .Msg-img>span{
       position:absolute;
       top:3.7rem;
        font-family: '宋体';
        color:#666;
        background: #fff;
        border-radius: 50%;
        width:0.8rem;
        height:0.8rem;
        line-height: 0.8rem;
        text-align: center;
        font-size:0.4rem;
        opacity:0;
  }
  @keyframes ltgt{
      0%{opacity:0;}
      100%{opacity:1;}
  }
  @keyframes ltgt2{
      0%{
          opacity:1;
      }
      100%{
          opacity:0;
      }
  }
  .lt{   
      left:0.2rem;
    
  }
  .gt{
      right:0.2rem; 
  }
  .Msg-img p{
      text-align: center;
      background: #D8D8D8;
      height:1rem;
      line-height:1rem;
      font-size:0.2rem;
      position:relative;
  }
  .Msg-img p span{
      position:absolute;
      right:1rem;
      opacity:0;

  }
  @keyframes dh {
      0%{
          opacity:0;
      }
      30%{
          opacity:1;
      }
      70%{
          opacity:1;
      }
      100%{
          opacity:0;
      }
  }
  .Msg-msg>p:nth-of-type(1){
      height:1.2rem;
      text-align: center;
      font-size:0.4rem;
      margin-top:0.4rem;
  }
  .Msg-msg>p:nth-of-type(2){
      text-align: center;
      font-size:0.3rem;
      margin-top:0.3rem;
    
  }
  .msg-msg>p{
      text-align: center;
      margin:0.6rem 0 0.5rem 0;
      font-size:0.2rem;

  }
  .msg-div{ 
       text-align: center;
      padding:0 0.2rem;
     
  }
  .msg-div p,.msg-div dd{
      color:#666;
    
  }
  .msg-div dl{
      margin-top:0.4rem;
      font-size:0.2rem;
  }
  .Size{
      display:flex;
      justify-content: flex-start;
      border-top:1px solid #999;
      border-bottom:1px solid #999;
  }
  .Size>p{
      width:20%;
      height:1.25rem;
      text-align: center;
      line-height: 1.25rem;
      font-size:0.2rem;
      border:1px solid #999;
  }
  .Size>div{
      width:80%;
      height:1.3rem;
      overflow: auto;
  }
  .Size>div ul{
      width:230%;
      overflow: hidden;
      display:flex;
      justify-content: flex-start;
     padding-left:0.3rem;
     background: #F6F6F6;
  }
   .Size>div::-webkit-scrollbar{
       display:none;
   }
  .Size>div ul li{
      height:1.25rem;
      width:1.3rem;
      border:1px solid #999;
      background: #fff;
      margin-right:0.3rem;
   text-align: center;
   line-height: 1.25rem;
  }
  .Msg-add{
      margin-top:0.3rem;
  }
  .Msg-add>p:nth-of-type(1){
      text-align: center;
      line-height: 1.2rem;
      height:1.2rem;
      background: #000;
      color:#fff;
      font-size:0.2rem;
      font-weight: bold;
  }
   .Msg-add>p:nth-of-type(2){
      text-align: center;
      line-height: 1.2rem;
      height:1.2rem;
      font-size:0.2rem;
      font-weight: bold;
      border-bottom:1px solid #999;
  }
  .Msg-help ul{
    display:flex;
    justify-content: flex-start;
    border-top:1px solid #999;
    border-bottom:1px solid #999;
    margin-top:0.4rem;
  }
  .Msg-help ul li{
      width:50%;
      text-align: center;
      line-height:1.2rem;
      height:1.2rem;
      font-size:0.2rem;
  }
  .Msg-help ul li:nth-of-type(1){
      border-bottom:2px solid;
  }
  .Msg-help>div{
      margin-top:0.3rem;
      position:relative; 
      width:100%; 
      height:3rem;
  }
  .Msg-help>div div{
  width:92%;
      height:3rem;
      text-align: center;
      font-size:0.2rem;
      padding:0 4%; 
      position:absolute;
      top:0;
      left:0;
      background: #fff;
  }
  .Msg-help-div1{
     z-index:888;
  }
 
</style>


