<template>
<div>
<div id='HEADER'>
    <div class='Header-mb' @click='mbclose()'></div>
<div class='Header'>
    <ul class='Header-ul'>
        <li @click='search()'><router-link to="#"><img src="@/assets/img/header1.jpg" alt=""></router-link></li>
        <li><router-link to="../index/collection"><img src="@/assets/img/header2.jpg" alt=""></router-link></li>
        <li><router-link to="#"><img src="@/assets/img/logo.svg" alt=""></router-link></li>
        <li><router-link to="../index/shop"><img src="@/assets/img/header3.jpg" alt=""></router-link></li>
        <li @click='moveLeft()'><router-link to="#"><img src="@/assets/img/header4.jpg" alt=""></router-link></li>
    </ul>
</div>
<div class='Header-Search'>
    <div class='Header-Search-header'>
     <ul>
         <li><img src="@/assets/img/search.jpg" alt=""></li>
         <li><input type="text" placeholder='搜索' id='searchInput'></li>
         <li @click='searchMsg()'><a href="#">开始搜索</a> </li>
         <li @click='close()'>×</li>
     </ul>
    </div>
    <p>最新搜索</p>
<ul class='Header-Search-ul'>
    <li v-for='searchdata in searchData' :key='searchdata.index'>{{searchdata}}</li>
</ul>
    <p @click='formatting()'>清除近期搜索</p>
</div>
<div class='Header-moveLeft'>
<ul class='Header-moveLeft-ul'>
   <li @click='nav("series")'><strong class='font-H'>系列</strong><span class='jt'>&gt;</span></li>
   <li @click='nav("woman")'><strong class='font-H'>女士</strong><span class='jt'>&gt;</span></li>
   <li @click='nav("man")'><strong class='font-H'>男士</strong><span class='jt'>&gt;</span></li>
   <li @click='nav("child")'><strong class='font-H'>孩子</strong><span class='jt'>&gt;</span></li>
   <li @click='nav("brand")'><strong class='font-H'>品牌</strong><span class='jt'>&gt;</span></li>
   <li><strong class='font-H'>门店</strong><span></span></li>
   <li><strong class='font-H'>美妆</strong><span></span></li>
   <li><span class='font-H'>区域 <strong>FR €</strong> </span><span class='jt'>&gt;</span></li>
   <li><span class='font-H'>语言</span><span class='jt'>&gt;</span></li>
   <li><span class='font-H'>联系方式</span><span></span></li>
   <li><span class='font-H'>登录</span><span></span></li>
</ul>
<div class='Header-moveLeft-div'>
    <dl>
        <dt><span class='jt' @click='NavLeftNice()'>&lt;</span> 
        <strong class='font-H'>{{msg2.title}}</strong></dt>
        <dd><img :src="msg2.img" alt=""></dd>
    </dl>
    <ul>
        <li v-for='list in msg2.msg' :key='list.index' @click='nav2(list)'>
            <span>{{list}}</span>
            <span class='jt'>&gt;</span>
        </li>
    </ul>
</div>

<div class='Header-moveLeft-div2'>
   <p><span class='jt' @click='NavLeftNice2()'>&lt;</span> 
        <strong class='font-H'>{{title}}</strong></p>
        <ul>
            <li v-for='list2 in three.shop' :key='list2.index' @click='List()'>
              <router-link to="#"> {{list2}}</router-link> 
            </li>
        </ul>
</div>
</div>
</div>
</div>
</template>

<script>
import Axios from 'axios'
export default {
    data(){
        return{
           searchData:['例：连衣裙'],
           msg:[],
           msg2:[],
           list:[],
           title:'',
           three:[],
           length:''
        }
    },
    methods:{
        List(){
            let title;
              let oHeader=document.getElementsByClassName('Header')[0];
             let oMb=document.getElementsByClassName('Header-mb')[0];
             let oHeaderUl=document.getElementsByClassName('Header-moveLeft')[0];
            oMb.style.display='none';
            //   oHeader.style.marginLeft='0';
            // oHeader.style.animation='moveright 0.5s 1';
            oHeaderUl.style.left='100%';
             oHeaderUl.style.animation='moveright2 0.5s 1';
         if(this.title=='包袋'){
            this.GLOBAL.title('纪梵希绗缝设计');
         }
         if(this.title=='鞋履'){
             this.GLOBAL.title('女士JAW SNEAKER运动鞋');
           
         }
        console.log(this.GLOBAL.Title);
         this.$router.push('/index/list'+title);
        },
        search(){    /*点击放大镜出现搜索页*/
           let oSearch=document.getElementsByClassName('Header-Search')[0];
           oSearch.style.display='block';
           window.onmousewheel=document.onmousewheel=()=>{
               return false;
           }
           window.onmousemove=document.onmousemove=()=>{
               return false;
           }

        },
        searchMsg(){   /*搜索页点击搜索按钮提示最近搜索记录*/
           let oSearchInput=document.getElementById('searchInput');
            if(oSearchInput.value!=''){
              this.searchData.unshift(oSearchInput.value);
           }
            if(this.searchData.length>4){
                this.searchData.pop();
            }
             oSearchInput.value='';
        },
        formatting(){ /*清空搜索记录*/
            this.searchData=[];
        },
         close(){/*关闭搜索页面 */
              let oSearch=document.getElementsByClassName('Header-Search')[0];
            oSearch.style.display='none';
            window.onmousewheel=document.onmousewheel=()=>{
               return true;
           }
         },
         mbclose(){
             let oHeader=document.getElementsByClassName('Header')[0];
             let oMb=document.getElementsByClassName('Header-mb')[0];
             let oHeaderUl=document.getElementsByClassName('Header-moveLeft')[0];
            oMb.style.display='none';
            //   oHeader.style.marginLeft='0';
            // oHeader.style.animation='moveright 0.5s 1';
            oHeaderUl.style.left='100%';
             oHeaderUl.style.animation='moveright2 0.5s 1';
         },
         moveLeft(){
        
             let oHeader=document.getElementsByClassName('Header')[0];
            let oMb=document.getElementsByClassName('Header-mb')[0];
            let oHeaderUl=document.getElementsByClassName('Header-moveLeft')[0];
        //    oHeader.style.marginLeft='-82%';
        //     oHeader.style.animation='moveLeft 0.5s 1';
             oHeaderUl.style.left='18%';
             oHeaderUl.style.animation='moveLeft2 0.5s 1';
            oMb.style.display='block';
           
         },
         nav(unknown){
           let url1='./static/data/headerNav.json';
           let oNavLeft=document.getElementsByClassName('Header-moveLeft-ul')[0];
           let oNavLeftNice=document.getElementsByClassName('Header-moveLeft-div')[0];
           Axios.get(url1).then(res=>{
               this.msg=res.data.xq;
            for(let i=0;i<this.msg.length;i++){
                if(this.msg[i].name==unknown){
                    this.msg2=this.msg[i];
                }  
            }        
           }).catch(
               ()=>{
                   alert('数据错误');
               }
           )
        oNavLeft.style.left='-100%';
        oNavLeft.style.animation='NavLeft 0.5s 1';
        oNavLeftNice.style.left='0';
        oNavLeftNice.style.animation='NavLeftNice 0.5s 1';
         },
         nav2(List){
              let oNavLeft=document.getElementsByClassName('Header-moveLeft-ul')[0];
        let oNavLeftNice=document.getElementsByClassName('Header-moveLeft-div')[0];
        let oNavLeftNice2=document.getElementsByClassName('Header-moveLeft-div2')[0];
        oNavLeftNice.style.left='-100%';
        oNavLeftNice.style.animation='NavLeft 0.5s 1';
        oNavLeftNice2.style.left='0%';
        oNavLeftNice2.style.animation='NavLeftNice 0.5s 1';
         this.title=List;
         
         for(let i=0;i<this.msg2.three.length;i++){
             if(this.msg2.three[i].name==List){
                 this.three=this.msg2.three[i];
             }
         }

            },
           NavLeftNice(){
           let oNavLeft=document.getElementsByClassName('Header-moveLeft-ul')[0];
           let oNavLeftNice=document.getElementsByClassName('Header-moveLeft-div')[0];
        oNavLeft.style.left='0';
        oNavLeft.style.animation='NavRight 0.5s 1';
        oNavLeftNice.style.left='100%';
        oNavLeftNice.style.animation='NavRightNice 0.5s 1';
       }, 
       NavLeftNice2(){ 
         let oNavLeft=document.getElementsByClassName('Header-moveLeft-ul')[0];
        let oNavLeftNice=document.getElementsByClassName('Header-moveLeft-div')[0];
        let oNavLeftNice2=document.getElementsByClassName('Header-moveLeft-div2')[0];
        oNavLeftNice.style.left='0';
        oNavLeftNice.style.animation='NavRight 0.5s 1';
        oNavLeftNice2.style.left='100%';
        oNavLeftNice2.style.animation='NavRightNice 0.5s 1';
       },
       

    },
   
}
</script>
<style>
/* 顶部header */

.font-H{
    font-size:0.23rem;
}
#HEADER{
    width:100%;
    overflow-x: hidden;

}
.Header-mb{
    width:100%;
    height:100%;
    background:rgba(73, 56, 56, 0.4);
    position:fixed;
    top:0;
    left:0;
    z-index:8887;
    display:none;
}
.Header{
    width:100%;
    height:1.3rem;
   background: #fff;
    position:fixed;
    top:0;
    left:0;
    z-index:8886;
  border-bottom:1px solid #999999;
}
@keyframes moveLeft {
    0%{margin-left:0;}
    100%{margin-left:-82%;}
}
@keyframes moveright {
    0%{margin-left:-82%;}
    100%{margin-left:0;}
}
.Header-ul{
    height:1.3rem;
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items:center;
   
}

.Header-ul>li:nth-of-type(3){
    width:38%;
  padding-top:10px;
}
.Header-ul>li:not(:nth-of-type(3)) a{
   width:36px;
   height:36px;
   display:flex;
   align-items: center;

}
.Header-ul>li:not(:nth-of-type(3)) a img{
   width:16px;
   height:16px;
}
.Header-ul>li:nth-of-type(1) a,.Header-ul>li:nth-of-type(5) a{
justify-content: center;
}
.Header-ul>li>a{
   outline: none;
}
.Header-ul>li:nth-of-type(4) a{
justify-content:flex-end;
}
/* 顶部header end */

/* header 的搜索 */
.Header-Search{
    width:100%;
    height:100%;
  position:fixed;
top:0;
left:0;   
  display:none;
  background: #fff;
  z-index:8889;
}
.Header-Search-header{
    height:1.3rem;
    width:100%;
background: #F6F6F6;
}
.Header-Search-header>ul{
    width:100%;  
    display:flex;
    justify-content:space-between;
}
.Header-Search-header>ul>li{

    text-align: center;
      height:1.3rem;
      line-height: 1.3rem;
}
.Header-Search-header>ul>li input{
    width:100%;
   border:0;
   border:none;
   outline:none;
   background: #F6F6F6;
   font-size:0.3rem;
}
.Header-Search-header>ul>li img{
width:0.3rem;
height:0.3rem;
}
.Header-Search-header>ul>li:nth-of-type(1){
width:12%; display:flex;
    align-items: center;
    justify-content: center;
}
.Header-Search-header>ul>li:nth-of-type(2){
width:43%;
}
.Header-Search-header>ul>li:nth-of-type(3){
width:25%;
background: #000;
}
.Header-Search-header>ul>li:nth-of-type(3) a{
    color:#fff;
    font-size:0.28rem;
    outline:none;
}
.Header-Search-header>ul>li:nth-of-type(4){
width:15%;
font-size:0.5rem;
}
.Header-Search p{
    width:100%;
    text-align: center;
}
.Header-Search p:nth-of-type(1){
font-size:0.33rem;
margin-top:3rem;
}
.Header-Search p:nth-of-type(2){
font-size:0.22rem;
  margin-top:0.5rem;
}
.Header-Search-ul,.Header-Search-ul li{
    width:100%;
    text-align: center;
    font-weight: bold;
    font-size:0.26rem;
}
.Header-Search-ul li{
    margin-top:0.5rem;
}
/* header 的搜索 end */

/* 向左滑块 */
.Header-moveLeft{
    width:85%;
    height:100%;
    position:fixed;
    top:0;
    left:100%;
    z-index:8888;
    background: #fff;
    overflow-x: hidden;
}
@keyframes moveLeft2 {
    0%{left:100%;}
    100%{left:18%;}
}
@keyframes moveright2{
     0%{left:18%;}
    100%{left:100%;}
}
.Header-moveLeft-ul{
    width:100%;
    position:absolute;
    top:0;
    left:0;
}

@keyframes NavLeft{
    0%{left:0;}
    100%{left:-100%;}
}
@keyframes NavRight{
     0%{left:-100%;}
    100%{left:0;}
}
.Header-moveLeft-ul li{
  border-bottom:1px solid #999999;
  padding:0.2rem 8%;
  display:flex;
  justify-content: space-between;
}
.Header-moveLeft-ul li:nth-of-type(8),
.Header-moveLeft-ul li:nth-of-type(9),
.Header-moveLeft-ul li:nth-of-type(10),
.Header-moveLeft-ul li:nth-of-type(11){
    border:none;
}
.Header-moveLeft-div{
    width:100%;
    position:absolute;
    top:0;
    left:100%;
}
@keyframes NavLeftNice{
    0%{left:100%;}
    100%{left:0;}
}
@keyframes NavRightNice{
    0%{left:0;}
    100%{left:100%;}
}
.Header-moveLeft-div dl dt,.Header-moveLeft-div2 p{
    text-align: center;
     padding:0.2rem 0;
     border-bottom:1px solid;
}
.Header-moveLeft-div dl span,.Header-moveLeft-div2 p span{
    position: absolute;
    top:0.2rem;
    left:6%;
}
.Header-moveLeft-div dl dd{
    height:3.8rem;
    overflow: hidden;
}
.Header-moveLeft-div ul li,.Header-moveLeft-div2 ul li{
    border-bottom:1px solid #999;
    padding:0.24rem 6%;
    font-size:0.22rem;
    display:flex;
    justify-content: space-between;
   align-items: center;
}
.Header-moveLeft-div2{
      width:100%;
    position:absolute;
    top:0;
    left:100%;
}

.Header-moveLeft-div2 ul li a{
    color:#000;
    outline:none;
}
.Header-moveLeft .Header-moveLeft-ul li span.jt
{
font-family:'黑体';
font-size:0.3rem;
color:#999;
}
/* .Header-moveLeft-ul .jt{
    font-family:'黑体';
    color:#666;
    font-size:0.3rem;
} */
/* 向左滑块 end */
</style>


