import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Global from '@/components/index/Global'
import Header from '@/components/common/Header'
import Footer from '@/components/common/Footer'
import Index from '@/components/index/Index'
import Homepage from '@/components/index/Homepage'
import List from '@/components/index/List'
import Msg from '@/components/index/Msg'
import Collection from '@/components/index/Collection'
import Shop from '@/components/index/Shop'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect:'/index/homepage'
    },{
      path:'/global',
      name:'global',
      component:Global
    },
    {
      path:'/header',
      name:'header',
      component:Header
    },{
      path:'/footer',
      name:'footer',
      component:Footer
    },{
      path:'/index',
      name:'index',
      component:Index,
      children:[
        {
        path:'homepage',
        name:'homepage',
        component:Homepage
      },{
        path:'list:title',
        name:'list',
        component:List
      },
      {
        path:'msg:Id',
        name:'msg',
        component:Msg, 
      },
      {
        path:'collection',
        name:'collection',
        component:Collection
      },
      {
        path:'shop',
        name:'shop',
        component:Shop
      },
      
    ]
    }
  ]
})



