<template>
<div>
    <div class='marginBottom'>
        <strong class='List-title'>{{list.name}}</strong>
        <div class='List-Nav-fixed'>
        <div class='List-Nav'>
            <div class='List-nav1'>
                <ul>
                    <li @click='listLi1()'>排序方式</li>
                    <li @click='listLi2()'>根据门店浏览</li>
                    <li @click='moveL()'>筛选器</li>
                </ul>
            </div>
            <div class='List-nav2'>
                <ul>
                    <li @click='moveR()' class='font'>&lt;</li>
                    <li><strong>筛选器</strong></li>
                    <li>现已上线</li>
                    <li>颜色</li>
                    <li>系列</li>
                </ul>
            </div>
        </div>
        <div class='List-NavTwo'>
          <!-- 二级菜单第一块 -->
            <div class='List-NavTwo-new'>
                <div class='rongqi'>
                <ul>
                    <li @click='sort("新款优先")'>新款优先</li>
                    <li @click='sort("默认排序")'>默认排序</li>
                </ul>
                <p class='List-NavTwo-p'>{{screen}}</p>
                <div class='List-NavTwo-div'>
                    <p><span> <strong>{{length}}结果</strong> </span>
                    <span @click='close()'><strong>关闭</strong></span></p>
                    
                </div>
            </div>
           </div>
        <!-- 二级菜单第二块 -->
           <div class='List-NavTwo-browse'>
          <div class='rongqi'>
              <p style='color:#666;font-size:0.2rem;padding:0.4rem 0;'>浏览目前旗舰店中的商品</p>
              <p style='font-size:0.2rem;color:#666;' @click='smallList1()'>法国+</p>
             <div class='small-List'>
               <div class='small-List-div'>
                   <ul>
                       <li v-for='dz in dz_fg.components' :key='dz.index' @click='dzClose()'>{{dz}}</li>
                   </ul>
               </div>
             </div>
             <p style='font-size:0.2rem;color:#666;' @click='smallList2()'>其他地址+</p>
             <div class='small-List'>
               <div class='small-List-div'>
                  <ul>
                       <li v-for='dz in dz_qt.components' :key='dz.index' @click='dzClose()'>{{dz}}</li>
                   </ul>
               </div>
             </div>
              <p class='List-NavTwo-p'>{{screen}}</p>
                <div class='List-NavTwo-div'>
                    <p><span> <strong>{{length}}结果</strong> </span>
                    <span @click='close()'><strong>关闭</strong></span></p>
                    
                </div>
          </div>
           </div>
        </div>
       </div> 
    </div>
    
    <div class='List-div'>
 <dl class='width47' v-for='list in listup' :key='list.index' >
 <dt><img :src="list.img[0]" alt="" @click='Msg(list.id)'></dt>
<dd class='List-dd1'><strong>{{list.title}}</strong></dd>
<dd class='List-dd2'>€{{list.money}}</dd>
<dd class='XIN'><span>{{list.pz}}</span><span @click='Collection(list.id)' class='collection'><img src="../../assets/img/xin.png" alt=""></span></dd>
 </dl>
 </div>
 <div class="List-div">
     <dl class='width100' v-for='list in listin' :key='list.index' >
    <dt><img :src="list.img[0]" alt="" @click='Msg(list.id)'> </dt>
    <dd class='List-dd1'><strong>{{list.title}}</strong></dd>
    <dd class='List-dd2'>€{{list.money}}</dd>
    <dd class='XIN'><span>{{list.pz}}</span><span @click='Collection(list.id)' class='collection'><img src="../../assets/img/xin.png" alt=""></span></dd>
</dl>
 </div>
  <div class="List-div2">
     <dl class='width42' v-for='list in listdown' :key='list.index'>
    <dt><img :src="list.img[0]" alt="" @click='Msg(list.id)'> </dt>
    <dd class='List-dd1'><strong>{{list.title}}</strong></dd>
    <dd class='List-dd2'>€{{list.money}}</dd>
    <dd class='XIN'><span>{{list.pz}}</span><span @click='Collection(list.id)' class='collection'><img src="../../assets/img/xin.png" alt=""></span></dd>
</dl>
 </div>
 <p class='display'><strong>显示{{listup.length+listin.length+listdown.length}}条，共{{length}}条</strong></p>
 <p class='more' @click='more()'><strong>查看更多产品</strong></p>
</div>

</template>
<script>
import Axios from 'axios'
export default {
    data(){
        return{
          list:[],
          list2:[],
          listup:[],
          listdown:[],
          listin:[],
          length:'',
          screen:'未选择筛选器',
          listL1:0,
          listL2:0,
          dz_fg:[],
          dz_qt:[],
        
        }
    },
    methods:{
       moveL(){
           let oNav1=document.getElementsByClassName('List-nav1')[0];
           let oNav2=document.getElementsByClassName('List-nav2')[0];
           oNav1.style.animation='moveL1 0.4s 1';
           oNav2.style.animation='moveL2 0.4s 1';
           oNav1.style.left='-100%';
           oNav2.style.left='0';
          
       },
       moveR(){
           let oNav1=document.getElementsByClassName('List-nav1')[0];
           let oNav2=document.getElementsByClassName('List-nav2')[0];
           oNav1.style.animation='moveR1 0.4s 1';
           oNav2.style.animation='moveR2 0.4s 1';
           oNav1.style.left='0';
           oNav2.style.left='100%';

       },
       more(){
           this.listdown=this.list.components.slice(3,this.list.components.length); 
       },
       sort(name){
          this.screen=name;
          this.listL1=0;
          this.listL2=0;  
            let oListNavTwo=document.getElementsByClassName('List-NavTwo')[0];
            oListNavTwo.style.display='none';
                for(let i=0;i<this.list.components.length;i++){
              if(this.list.components[i].pz=='新品'){
                this.list.components.unshift(this.list.components[i]);
                this.list.components.splice(i+1,1);
                
              }
          }
      
          
         
        
       }, 
       
       listLi1(){
          let oListNavTwo=document.getElementsByClassName('List-NavTwo')[0];
          let oListNavTwonew=document.getElementsByClassName('List-NavTwo-new')[0];
           let oListNavTwobrowse=document.getElementsByClassName('List-NavTwo-browse')[0];
           this.listL1++;
           this.listL2=0;
          if(this.listL1%2==1){
              oListNavTwo.style.display='block'; 
             oListNavTwonew.style.zIndex='668';
             oListNavTwobrowse.style.zIndex='667';
          }else{
               oListNavTwo.style.display='none'; 
          }
         console.log('l1',this.listL1)
         
       },
       listLi2(){
          let oListNavTwo=document.getElementsByClassName('List-NavTwo')[0];
            let oListNavTwonew=document.getElementsByClassName('List-NavTwo-new')[0];
          let oListNavTwobrowse=document.getElementsByClassName('List-NavTwo-browse')[0];
           this.listL2++;
         this.listL1=0;
           if(this.listL2%2==1){
              oListNavTwo.style.display='block'; 
              oListNavTwonew.style.zIndex='667';
             oListNavTwobrowse.style.zIndex='668';
          }else{
               oListNavTwo.style.display='none'; 
          }
         console.log('l2',this.listL2);
       },
       close(){
           this.listL1=0;
           this.listL2=0;
           let oListNavTwo=document.getElementsByClassName('List-NavTwo')[0];
           oListNavTwo.style.display='none'; 
            
       },
       dzClose(){
         let oListNavTwo=document.getElementsByClassName('List-NavTwo')[0];
         oListNavTwo.style.display='none';
         this.listL1=0;
           this.listL2=0;
       },
       smallList1(){
           let url='./static/data/dizhi.json';
        let oSmallLest1=document.getElementsByClassName('small-List')[0];
        let oSmallLest2=document.getElementsByClassName('small-List')[1];
        oSmallLest1.style.display='block';
        oSmallLest2.style.display='none';
        Axios.get(url).then(res=>{
            this.dz_fg=res.data.dz[0];
        }).catch(()=>{
            alert('城市数据错误');
        })
    }, 
      smallList2(){
          let url='./static/data/dizhi.json';
        let oSmallLest1=document.getElementsByClassName('small-List')[0];
        let oSmallLest2=document.getElementsByClassName('small-List')[1];
        oSmallLest1.style.display='none';
        oSmallLest2.style.display='block';
          Axios.get(url).then(res=>{
            this.dz_qt=res.data.dz[1];
        }).catch(()=>{
            alert('城市数据错误');
        })
    },
    Msg(id){
       this.$router.push('/index/msg'+id);
    },
    Collection(ID){
        let oCollection=document.getElementsByClassName('collection');
  this.GLOBAL.setId(ID);
  for(let i=0;i<oCollection.length;i++){
      oCollection[i].onclick=()=>{
          oCollection[i].innerHTML="<img src='./static/img/xin2.png' />";
      }
  }
    }
    },
   
    mounted(){
         let url='./static/data/shop.json';
        Axios.get(url).then(res=>{
            for(let i=0;i<res.data.shop.length;i++){
                if(res.data.shop[i].name==this.GLOBAL.Title){
                    this.list=res.data.shop[i];
                }
            }
         console.log('title===>',this.GLOBAL.Title);
            this.listup=this.list.components.slice(0,2);
            this.listin=this.list.components.slice(2,3);
            this.listdown=this.list.components.slice(3,15);
            this.length=this.list.components.length;
           
        }).catch(
            ()=>{
                alert('数据错误');
            }
        )
       window.onscroll=()=>{
           let oListNavFixed=document.getElementsByClassName('List-Nav-fixed')[0];
            let oScroll=document.documentElement.scrollTop;
            if(oScroll>='50'){
              oListNavFixed.style.position='fixed';
               oListNavFixed.style.top='1.3rem';
            }else{
                  oListNavFixed.style.position='static';
            
            }
        console.log('卷上',oScroll);
       }
    }

}
</script>
<style>
 .List-title{
     width:100%;
     height:1.4rem;
     line-height: 1.4rem;
     font-size:0.42rem;
     text-align: center;
     display:block;
     border-bottom:1px solid #999999;
    
 } 
 .marginBottom{
     margin-bottom:1rem;
 }
 .List-Nav-fixed{
     z-index:8666;
     width:100%;
 }
 .List-Nav{
     width:100%;
     height:1rem;
     position:relative;
     overflow: hidden;
     border-bottom:1px solid #999;
   
     background: #fff;
 }
 .List-Nav>div{
     height:1rem;
     position:absolute;
     width:100%;
 }
 .List-Nav>div ul{
     width:100%;
     height:1rem;
     line-height: 1rem;
     display:flex;
     justify-content: space-around;
     font-size:0.2rem;
    
 }
 .List-nav1{
     top:0;
     left:0;
 }
 .List-nav2{
     top:0;
     left:100%;
 }
 .List-NavTwo{
      height:8.9rem;
        background: #fff;
     display:none;
     position: relative;
 }
 .rongqi{
     position:relative; 
      height:100%; 
 }
 .List-NavTwo-new,.List-NavTwo-browse{
     position:absolute;
     top:0;
    left:0;
    width:84%;
     padding:0 8%;
       height:100%; 
       background: #fff;
 }
 .List-NavTwo-new ul li{
     height:0.8rem;
     line-height: 0.8rem;
     font-size:0.2rem;
 }
 
 .List-NavTwo-p{
     color:#666;
     font-size:0.2rem;
     position:absolute;
     bottom:1.6rem;
 }
 .List-NavTwo-div{
     width:84%;
     font-size:0.2rem;
     position:absolute;
     bottom:0.3rem;
 }
 .List-NavTwo-div p{
display:flex;
     justify-content: space-between;
 }
 .small-List{
     height:4.5rem;
     overflow: scroll;
     display: none;
 }
 .small-List-div{
height:7rem;
overflow:hidden;
 }
  .small-List-div ul li{
      height:0.8rem;
      color:#666;
      font-size:0.2rem;
  }
 .font{
     font-family: '宋体';
     font-size:0.32rem;
 }
 @keyframes moveL1{
  0%{left:0;}
  100%{left:-100%;}
 }
 @keyframes moveR1{
  0%{left:-100%;}
  100%{left:0;}
 }
  @keyframes moveL2{
  0%{left:100%;}
  100%{left:0;}
 }
 @keyframes moveR2{
  0%{left:0;}
  100%{left:100%;}
 }
/* 以下是商品列表 */

 .List-div{
     padding:0 5%;
       display:flex;
  justify-content:space-between;
 } 
 .List-div2{

     overflow:hidden;
     padding-right:0 5%;
 }
 .List-div2>dl{
float:left;
margin-left:5%;
 }
  .List-div>dl,.List-div2>dl{
  position: relative;
  margin-bottom:0.5rem;
  } 
 .width47{
  width:47.5%;
 }
 .width42{
  width:42.5%;
 }
 .width100{
     width:100%;
 }
  .List-dd1{
      font-size:0.23rem;
      margin-top:0.15rem;
      text-align: center;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
  }
  .List-dd2{
      font-size:0.2rem;
    line-height: 0.3rem;
    text-align: center;
  }
 .XIN{
     width:90%;
    display:flex;
    justify-content: space-between;
     position:absolute;
     top:0.2rem;
     left:5%;
 }
 .XIN span{
     display:block;
     font-size:0.2rem;
 }
  .XIN span img{
position:relative;
top:0.1rem;
       width:0.25rem;
     height:0.2rem;
  }
  .font{
      font-family:'宋体';
      font-size:0.4rem;
      font-weight: bold;
  }
  .display{
      width:90%;
      height:1rem;
      line-height: 1rem;
      margin:0 auto;
      border-bottom:1px solid #000;
      text-align: center;
      font-size:0.2rem;
  }
  .more{
      width:90%;
      margin:0 auto 1rem auto;
      text-align: center;
      font-size:0.2rem;
      height:0.8rem;
      line-height: 0.8rem;
  }
</style>


