<template>
<div>
  <div class='bt'><p>购物袋</p></div> 
   <div>
       <ul class='shop-ul'>
           <li v-for='list in shop2' :key='list.index'>
               <div class='shopLeft'><img :src="list.img[0]" alt=""></div>
            <div class="shopRight">
                <ul>
                    <li><strong>{{list.title}}</strong></li>
                    <li><strong>价格：</strong>€{{list.money}}</li>
                    <li><strong>产品编号：</strong>{{list.number}}</li>
                    <li><strong>产品类型：</strong>{{list.other}}</li>
                    <li><strong>数量：</strong>1</li>
                    <li><strong>价格：</strong><span class='jg'>€{{list.money}}</span></li>
                </ul>
                <div><span>提交</span><span>删除</span></div>
            </div>
           </li>
       </ul>
   </div>
   <div class='More'>
     <p>显示{{shop2.length}}条，共{{shop.length}}条</p>
 <p @click='more()'>查看更多购物袋</p>
</div>
</div>
</template>
<script>
import Axios from 'axios'
export default {
    data(){
        return{
            shop:[],
            shop2:[],
        }
    },
    methods:{

  more(){
      this.shop2=this.shop;
  }
    },
    mounted(){
          let url='./static/data/shop.json';
        Axios.get(url).then(res=>{
          for(let i=0;i<res.data.shop.length;i++){
              for(let j=0;j<res.data.shop[i].components.length;j++){
                  for(let n=0;n<this.GLOBAL.Shop.length;n++){
                      if(res.data.shop[i].components[j].id==this.GLOBAL.Shop[n]){
                          this.shop.push(res.data.shop[i].components[j]);
                      }
                  }
              }
          }
          this.shop=this.shop.reverse();
         this.shop2=this.shop.slice(0,10);
         console.log(this.shop2);
        }).catch(
            ()=>{
                alert('数据错误');
            }
        )
  
    }

}
</script>
<style>
   .bt{
    height:1.2rem;
    margin-bottom:1rem;
}
 .bt>p{
width:100%;
text-align: center;
font-size:0.3rem;
font-weight: bold;
line-height:1.2rem;
height:1.2rem;
border-bottom:1px solid #999;
position:fixed;
z-index:999;
background: #fff;
 }
    .shop-ul{
        width:100%;
   
    }
    .shop-ul>li{
    overflow: hidden;
    width:100%;
    background: #F6F6F6;
    padding:0.3rem 0;
    margin-bottom:0.3rem;
    }
    .shopLeft{
        float:left;
        width:40%;
        overflow: hidden;
    }
    .shopRight{
        float:right;
        width:57%;
    }
     .shopRight li{
         font-size:0.2rem;
         margin-bottom:0.1rem;
     }
     .shopRight li:nth-of-type(4),.shopRight li:nth-of-type(5){   
         margin-bottom:0;
     }
     .shopRight>div span{
         margin-right:0.5rem;
     }
     .jg{
         color:red;
         font-size:0.25rem;
         font-weight: bold;
         position:relative;
         top:0.02rem;
     }
     .More>p:nth-of-type(1){
      width:92%;
      height:1rem;
      line-height:1rem;
      margin:0 auto;
      border-bottom:1px solid;
      text-align: center;
      font-size:0.2rem;
  }
  .More>p:nth-of-type(2){
      text-align: center;
      line-height: 1rem;
      height:1rem;
      font-weight: bold;
      font-size:0.2rem;
      margin-bottom:0.8rem;
  }
</style>


