<template>
    
</template>
<script>
export default {
          arr:[],
         Shop:[],
         Title:'',
     setId(id){       
      this.arr.unshift(id);  
          for(let i=0;i<this.arr.length;i++){
            for(let j=this.arr.length-1;j>i;j--){
                if(this.arr[i]==this.arr[j]){
                    this.arr.splice(i,1);
                }
            }
        }
      console.log(this.arr);
   },
  
   shop(id){
    this.Shop.unshift(id);
       for(let i=0;i<this.Shop.length;i++){
            for(let j=this.Shop.length-1;j>i;j--){
                if(this.Shop[i]==this.Shop[j]){
                    this.Shop.splice(i,1);
                }
            }
        }
      console.log('shop',this.Shop);
   },
   title(t){
  this.Title=t;
   }
  
  
   
}
</script>
<style>
    
